import Image from "next/image";
import Link from "next/link";
import { RxHamburgerMenu } from "react-icons/rx";
// import { useSession } from "next-auth/react"; // Or replace with your session logic

export default function Header() {
  //   const { data: session } = useSession(); // Replace with your actual auth/session logic

  return (
    <div
      id="headicons"
      className="fixed top-0 w-full z-50"
      style={{ scrollBehavior: "smooth" }}
    >
      <div
        className=""
        style={{ display: "flex", justifyContent: "space-between" }}
      >
        {/* Logo */}
        <Link href="/">
          <Image
            src="/images/PinkMirror-Favicon.png"
            alt="PinkMirror Logo"
            width={70}
            height={70}
            style={{ margin: "11px 0" }}
          />
        </Link>
        {/* Welcome Text (if logged in) */}
        {/* {session?.user?.name && (
          <div id="login_YES" className="mt-4 text-center">
            <h6 className="text-orange-500 capitalize">
              Welcome {session.user.name}
            </h6>
          </div>
        )} */}
        {/* Login Button (if not logged in) */}
        {/* <Link href="/login">
          id="login_NO" className="page-title-icon shadow-xl bg-theme text-white
          rounded-full p-2 mx-2" style={{ marginTop: "27px" }}
          <i className="fas fa-sign-in-alt"></i>
        </Link> */}
        <div
          className="page-title-icon shadow-xl bg-theme text-white rounded-full p-2 mx-2"
          style={{
            marginTop: "27px",
            backgroundColor: "rgb(247, 148, 29)",
          }}
          data-menu="menu-main"
        >
          <RxHamburgerMenu />
        </div>
      </div>
    </div>
  );
}
